$alphabet-width: 3rem;
$delete-button-width: 2rem;
$status-width: 4rem;
$text-length-width: 3rem;
$hover-color: #eeeeee;
$active-color: #dddddd;
$output-button-color: #00ebc7;
$output-button-hover-color: #00dab9;
$output-button-active-color: #00c9aa;

#root {
    width: 100%;
}

.tool-bar {
    display: flex;
    justify-content: space-between;
    border-bottom: solid thin black;
    margin: 1rem 0;
}

.tool-bar-edge {
    width: 5rem;
    margin: 0 1rem;
    display: flex;
}

.tool-bar-button {
    margin: auto 0;
    padding: 1rem 1rem;
}

.tool-bar-button:hover {
    background-color: $hover-color;
}

.tool-bar-button:active {
    background-color: $active-color;
}

h1 {
    text-align: center;
    margin: 0;
}

ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

button {
    background-color: transparent;
    border: none;
    cursor: default;
    outline: none;
    padding: 0;
    appearance: none;
}

.root-flex-container {
    display: flex;
}

.root-flex-item {
    padding: 0 1rem;
    width: 50%;
}

.subtitle {
    font-size: 1.2rem;
}

.content-large {
    margin-bottom: 2rem;
}

.content-small {
    margin-bottom: 1rem;
}

.indent {
    margin-left: 1rem;
}

.add-button {
    margin-top: 0.2rem;
    padding: 0.2rem 0;
    border-top: solid thin black;
    width: 100%;
}

.add-button:hover {
    background-color: $hover-color;
}

.add-button:active {
    background-color: $active-color;
}

.alphabet {
    width: $alphabet-width;
}

.delete-button {
    width: $delete-button-width;
    display: flex;
    align-items: center;
}

.text-length {
    width: $text-length-width;
    text-align: right;
}

.icon {
    margin: 0 0.4rem;
}

// タスク系
.task-item-flex-container {
    display: flex;
    margin-bottom: 0.8rem;
}

.task-item-content {
    width: calc(100% - #{$alphabet-width} - #{$delete-button-width});
    margin-right: 0.8rem;
}

.task-item-content-lower {
    display: flex;
}

.task-item-vertical-center {
    display: flex;
    align-items: center;
}

.task-item-title {
    width: 100%;
}

.task-item-status {
    width: $status-width;
}

.task-item-comment {
    width: calc(100% - #{$status-width});
}

.delete-button:hover {
    background-color: $hover-color;
}

.task-item-flex-container {
    display: flex;
    margin-bottom: 0.8rem;
}

// コメント系
.content-item-flex-container {
    display: flex;
    margin-bottom: 0.8rem;
}

.content-item-comment {
    width: calc(100% - #{$alphabet-width} - #{$text-length-width} - #{$delete-button-width});
}

.output {
    width: 100%;
    height: 100%;
}

.output-button {
    margin-top: 0.6rem;
    width: 100%;
    height: 2rem;
    border-radius: 4px;
    background-color: $output-button-color;
    display: flex;
    justify-content: center;
    align-items: center;
}

.output-button:hover {
    background-color: $output-button-hover-color;
}

.output-button:active {
    background-color: $output-button-active-color;
}
